.accordion {
  text-align: left;
  display: flex;
  flex-direction: column;
}

.accordion__title {
  font-size: 18px;
  color: #041434;
  font-weight: 500;
  cursor: pointer;
  margin-right: 20px;
}

.accordion__title-box {
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.accordion__circle {
  height: 25px;
  width: 25px;
  border-radius: 50%;
  border: 3px solid #20AD96;
  padding-top: 7.5px;
  padding-left: 4px;
  flex-shrink: 0;
  transition: transform 0.3s ease-in-out;
}

.accordion__circle span {
  display: block;
  transform-origin: 0% 0%;
  width: 11px;
  height: 3px;
  background-color: #20AD96;
  transition: 0.3s ease-in-out;
}

.accordion__circle span:last-child {
  transform: rotate(90deg) translateX(-7px) translateY(-7px);
}

.accordion__circle.active {
  transform: rotate(90deg);
  padding-left: 5px;
}

.accordion__circle.active span:last-child {
  display: none;
  transition-delay: 0.3s;
}

.accordion__circle.active span:first-child {
  transform: rotate(90deg) translateX(-4px) translateY(-6px);
}

.accordion__text {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.accordion__descr {
  padding: 0;
  background-color: white;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease-in-out;
}

.accordion__descr.active {
  max-height: 50px;
  display: block;
}

.accordion__item {
  margin-bottom: 24px;
  padding-bottom: 32px;
  border-bottom: 1px solid #E4E7EC;
}

@media screen and (max-width: 1300px) {
  .accordion__circle {
    padding-left: 3.5px;
    padding-top: 7.5px;
  }
  .accordion__circle.active {
    padding-left: 4px;
  }
  .accordion__circle.active span:first-child {
    transform: rotate(90deg) translateX(-4px) translateY(-7px);
  }
}
@media screen and (max-width: 900px) {
