.subscribe
    background-color: $color-bg
    padding: 48px 0
    &__wrapper  
        display: flex
        justify-content: space-between
        align-items: center
    &__left-wrapper
        margin-right: 20px
    &__title
        font-size: 30px
        font-weight: 800
        line-height: 38px
        color: $color-title
        margin-bottom: 16px
    &__descr
        font-size: 20px
        font-weight: 400
        line-height: 30px
    &__input
        width: 280px
    &__btn
        width: 150px
        font-weight: 500


@media screen and ( max-width: 1100px )
    .subscribe
        text-align: center
        &__wrapper
            flex-wrap: wrap
            justify-content: center
            row-gap: 20px
        &__input
            margin-bottom: 15px
            max-width: 600px