.about-top
    background-color: $color-bg
    &__wrapper
        display: flex
        align-items: center
        justify-content: space-between
    &__descr
        margin-bottom: 23px
    &__link
        border-radius: 10px
        width: 250px
        display: block
    &__left-wrapper
        max-width: 545px
        margin-right: 30px
    &__right-wrapper
        padding-top: 100px
        padding-right: 150px
    &__inner
        position: relative
    &__calendar
        position: absolute
        top: -65px
        left: -90px
        width: 450px
        height: 225px
    &__comment
        display: flex
        justify-content: space-between
        align-items: center
        flex-direction: column
        background-color: #fff
        padding: 32px 40px
        max-width: 194px
        position: absolute
        top: 206px
        right: -130px
    &__message
        width: 39px
        height: 39px
        margin-bottom: 24px
    &__online
        color: $color-title-h2

@media screen and ( max-width: 1100px)
    .about-top
        &__img
            width: 383px
            height: 500px
        &__comment
            padding: 24px 30px
            right: -110px
            top: 180px

@media screen and ( max-width:  996px)
    .about-top
        &__img
            width: 306px
            height: 400px
        &__more
            font-size: 14px
        &__online
            font-size: 14px
        &__comment
            padding: 16px 20px
            right: -90px
            top: 140px
        &__message
            margin-bottom: 10px
        &__calendar
            height: 180px
            top: -51px
            left: -125px
        &__right-wrapper
            padding-right: 100px
        
@media screen and ( max-width:  900px)
    .about-top
        &__wrapper
            flex-direction: column-reverse
        &__right-wrapper
            padding-right: 0
            padding-top: 50px
            margin-bottom: 30px
        &__left-wrapper
            max-width: 100%
            text-align: center
            padding-bottom: 40px
        &__title
            max-width: 100%
        &__link
            display: inline-block

@media screen and ( max-width:  500px)
    .about-top
        &__img
            width: 230px
            height: 300px
        &__more
            font-size: 12px
        &__online
            font-size: 12px
        &__comment
            padding: 10px 15px
            right: -70px
            top: 100px
        &__message
            margin-bottom: 10px
            width: 30px
            height: 30px
        &__calendar
            height: 130px
            top: -38px
            left: -150px