.company
    padding: 96px 0
    text-align: center
    &__descr
        font-family: Inter
        font-size: 16px
        font-weight: 500
        color: $color-desc
        line-height: 24px
        margin-bottom: 32px
    &__box
        display: flex
        justify-content: center
        align-items: center
    &__logo
        height: 48px
        &:hover
            fill: $color-green
            
@media screen and ( max-width:  1400px)
    .company
        &__logo
            margin: 0 -20px

@media screen and ( max-width:  1250px)
    .company
        &__logo
            margin: 0 -45px
        

@media screen and ( max-width:  1100px)
    .company
        &__box
            flex-wrap: wrap
        &__logo
            margin: 0
            margin-bottom: 20px

        
@media screen and ( max-width:  768px)
    .company
        padding: 60px 0

@media screen and ( max-width:  568px)
    .company
        padding: 30px 0
        &__descr
            font-size: 14px