.top-services
    padding: 80px 0
    background-color: $color-bg
    margin-bottom: 80px
    &__wrapper
        display: flex
        align-items: center
        justify-content: space-between
@media screen and ( max-width:  900px)
    .top-services
        &__left-wrapper
            margin-right: 30px
        &__right-wrapper
            padding-right: 30px
        &__img
            width: 350px

@media screen and ( max-width:  800px)
    .top-services__img
        width: 300px
    
@media screen and ( max-width:  700px)
    .top-services
        padding: 40px 0
        margin-bottom: 60px
        &__text-box
            padding-top: 20px
        &__wrapper
            flex-direction: column-reverse
        &__left-wrapper
            margin-right: 0
        &__right-wrapper
            padding-right: 0