.download
    text-align: center
    position: relative
    &__content
        display: flex
        flex-direction: column
        align-items: center
    &__title
        max-width: 720px
    &__descr
        max-width: 540px
        margin-bottom: 40px
        font-size: 17px
        opacity: 0.8
    &__btn
        width: 250px
        margin-bottom: 80px
    &__rating
        margin-bottom: 8px
        opacity: 0.5
    &__avatar-1
        transform: translateX(-350px) translateY(-360px)
    &__avatar-2
        transform: translateX(-650px) translateY(-200px)
    &__avatar-3
        transform: translateX(400px) translateY(-500px)
    &__avatar-4
        transform: translateX(600px) translateY(-350px)
        width: 42px
        height: 42px
    &__avatar-5
        transform: translateX(375px) translateY(-140px)

@media screen and ( max-width:  1450px)
    .download
        &__avatar-2
            transform: translateX(-300px) translateY(-200px)
        &__avatar-4
            transform: translateX(300px) translateY(-350px)
            width: 42px
            height: 42px
        &__avatar-5
            transform: translateX(90px) translateY(-140px)

@media screen and ( max-width:  1100px)
    .download
        &__avatar-1
            display: none
        &__avatar-2
            transform: translateX(-170px) translateY(-200px)
        &__avatar-3
            transform: translateX(-330px) translateY(-310px)

@media screen and ( max-width:  820px)
    .download
        &__btn
            margin-bottom: 40px
        &__descr
            font-size: 16px
        &__avatar-3
            display: none
        &__avatar-2
            transform: translateX(-200px) translateY(-170px)
        &__avatar-5
            transform: translateX(140px) translateY(-50px)
    
@media screen and ( max-width:  676px)
    .download
        &__avatar-4
            display: none

@media screen and ( max-width:  556px)
    .download
        &__descr
            margin-bottom: 20px
            font-size: 14px
        &__btn
            width: 200px
            margin-bottom: 20px
        &__rating
            font-size: 14px
        &__avatar-2
            transform: translateX(-130px) translateY(-60px)
        &__avatar-5
            transform: translateX(125px) translateY(-115px)

@media screen and ( max-width: 425px)
    .download
        &__avatar-2
            width: 45px
            height: 45px
            transform: translateX(-110px) translateY(-60px)
        &__avatar-5
            width: 45px
            height: 45px
            transform: translateX(110px) translateY(-115px)
    