.place
    position: relative
    width: 446px
    height: 581px
    background: linear-gradient(210deg, #333461 -85.62%, #483D8B 25.45%, #353565 165.27%)
    &::after   
        content: ""
        background-image: url('../img/free-guide.png')
        position: absolute
        z-index: 2
        width: 341px
        height: 364px
        background-repeat: no-repeat
        background-size: cover
        background-position: center
        bottom: -0
        right: 0
    &__free
        position: absolute
        background-color: $color-green
        font-weight: 800
        font-size: 17px
        width: 96px
        height: 96px
        border-radius: 50% 
        color: white
        display: flex
        align-items: center
        justify-content: center
        right: -48px
        top: -48px
    &__text
        position: absolute
        left: 40px
        top: 50px
        z-index: 15
        text-align: left
        font-size: 48px
        font-weight: 800
        line-height: 130%
        color: #fff
        max-width: 240px
        
    &__img
        width: 45px
        height: 45px
    &__inner
        position: absolute
        bottom: 25px
        z-index: 5
        width: 100%
        height: 85px
        background: rgba(65, 58, 125, 0.30)
        color: #fff
        display: flex
        justify-content: center
        align-items: center
        column-gap: 5px
        cursor: pointer
        transition: background 0.3s ease-in-out
        &:hover
            background: rgba(65, 58, 125, 0.45)
    &__title
        font-weight: 700
        margin-bottom: -5px
    &__text-inner
        font-size: 10px
        font-weight: 500


@media only screen and (max-width: 1024px)
    .place
        width: 350px
        height: 500px
        &__text
            font-size: 40px
            left: 30px

@media only screen and (max-width: 800px)
    .place
        width: 350px
        height: 500px
        &__text
            font-size: 36px
            left: 0
        &::after
            width: 250px
            height: 266px
        &__free
            width: 80px
            height: 80px
            top: -40px
            right: -40px
            font-size: 14px
        &__text
            font-size: 36px
            padding-left: 30px

@media only screen and (max-width: 768px)
    .place__before
            &::after
                left: -35px

@media only screen and  (max-width: 700px)
    .place
        width: 350px
        height: 500px

@media (max-width: 480px)
    .place
        width: 300px
        height: 450px
        &__text
            font-size: 30px
            max-width: 200px

@media (max-width: 440px)
    .place
        width: 230px
        height: 370px
        &::after
            width: 180px
            height: 180px
        &__text
            font-size: 25px
            max-width: 200px
        &__title
            font-size: 14px
            margin-bottom: 0
        &__text-inner
            font-size: 9px
            line-height: 130%
