.top-free
    margin-bottom: 120px
    &__wrapper
        display: flex
        align-items: center
        justify-content: space-between
    &__left-wrapper
        max-width: 50%
        margin-right: 32px
    &__title
        font-size: 48px
        color: $color-title
        font-weight: 800
        line-height: 58px
        letter-spacing: -0.96px
        margin-bottom: 24px
    &__descr
        font-size: 20px
        line-height: 30px
        opacity: 0.7
        margin-bottom: 40px
    &__form
        display: flex
        flex-direction: column
        gap: 20px
        max-width: 300px
    &__privacy
        font-family: Inter
        font-size: 14px
        font-weight: 400
        line-height: 20px /* 142.857% */
    & input
        margin-right: 0
    &__privacy-link
        text-decoration: underline
        transition: .3s ease-in-out
        &:hover
            color: $color-green
    &__right-wrapper
        flex-shrink: 0
        background-color: $color-bg
        padding: 70px


@media screen and ( max-width:  900px)
    .top-free
        margin-bottom: 80px
        &__wrapper
            display: flex
            flex-direction: column-reverse
            align-items: center
            justify-content: space-between
        &__right-wrapper
            padding: 70px
        &__left-wrapper
            max-width: 100%
            padding: 30px
        &__title
            font-size: 40px
        &__descr
            font-size: 18px

@media screen and ( max-width:  556px)
    .top-free
        margin-bottom: 40px
        &__title
            font-size: 36px
            margin-bottom: 5px
        &__descr
            font-size: 16px
            margin-bottom: 20px