.extra-top
    padding-top: 30px
    &__wrapper
        display: flex
        align-items: center
        justify-content: space-between
    &__left-wrapper
        max-width: 545px
        margin-right: 30px
    &__descr
        margin-bottom: 25px

@media (max-width: 1100px)
    .extra-top
        &__img
            width: 400px

@media (max-width: 1000px)
    .extra-top
        &__img
            width: 350px

@media (max-width: 900px)
    .extra-top
        &__wrapper
            flex-direction: column-reverse
        &__right-wrapper
            margin-bottom: 20px
        &__left-wrapper
           text-align: center
           margin-right: 0
           max-width: 100%
        &__title
            max-width: 100%

@media (max-width: 400px)
    .extra-top
        &__img
            width: 300px