.header
    height: 85px
    &__wrapper
        max-width: 1360px
        padding: 0 15px
        margin: 0 auto
        display: flex
        justify-content: space-between
        align-items: center
        height: 85px
    &__btn
        font-weight: 600
    .nav
        display: flex
        justify-content: space-between
        align-items: center
        column-gap: 30px
    &__list
        display: flex
        justify-content: space-between
        align-items: center
        column-gap: 30px
    &__link
        transition: 0.3s
        font-size: 15px
        &:hover
            color: $color-green
    
.burger
    display: none
    width: 1px

@media only screen and (max-width: 800px)
    .header
        position: relative
        &__list
            padding: 15px 0
            position: absolute
            width: 100%
            min-height: 50vh
            background-color: #fff
            flex-direction: column
            z-index: 5
            row-gap: 30px
            justify-content: center
            opacity: 0.8
            background-color: #000
            top: 0
            left: 0
            transform: translateX(-150%)
            transition: transform 0.5s ease
            font-weight: 800
            font-size: 20px
            &.active
                top: 0
                transform: translateX(0)
    .burger
        display: block
        position: relative
        z-index: 7
        width: 32px
        height: 26px
        cursor: pointer
        &.active
            & span
                &:nth-child(2)
                    transform: scaleY(0)
                &:first-child
                    
                    transform: rotate(45deg)
                &:last-child
                    transform: rotate(-45deg)
        & span
            display: block
            width: 32px
            height: 4px
            border-radius: 2px
            background-color: $color-green
            margin-bottom: 7px
            &:first-child
                transform-origin: 0% 0%
                transition: transform 0.4s ease-in-out
            &:last-child
                transform-origin: 0% 100%
                transition: transform 0.4s ease-in-out
                margin-bottom: 0
            &:nth-child(2)
                transition: transform 0.2s ease-in-out
                
@media screen and ( max-width:  480px)
    .header
        &__wrapper
            gap: 15px
        &__btn
            font-size: 12px
            padding: 8px 11px