.features
    margin-bottom: 120px
    text-align: center
    &__h2
        margin-bottom: 20px
    &__subtext
        margin-bottom: 64px
        font-size: 20px
        opacity: 0.7
    &__wrapper
        display: flex
        align-items: center
        justify-content: space-between
    &__img
        object-fit: cover
        background-color: $color-bg
        width: 55px
        height: 55px
        border-radius: 28px   
        flex-shrink: 0            
    &__left-wrapper
        max-width: 560px
        text-align: left
        display: flex
        flex-direction: column
        align-items: start
        row-gap: 48px
        margin-right: 20px
    &__item
        display: flex
        column-gap: 16px
    &__images
        width: auto
        object-fit: cover

@media screen and ( max-width: 1024px)
    .features
        &__image
            width: 450px

@media screen and ( max-width: 900px)
    .features
        &__wrapper
            flex-direction: column-reverse
        &__subtext
            margin-bottom: 20px
            font-size: 18px
        &__image
            margin-bottom: 20px
        &__left-wrapper
            margin-right: 0

@media screen and ( max-width: 480px)
    .features
        &__image
            width: 350px
        &__subtext
            font-size: 16px
        &__img
            width: 45px
            height: 45px
            border: none
            background-color: #fff
            border-radius: 0