*
    box-sizing: border-box
   
html
     width: 100%
     overflow-x: hidden
a
    text-decoration: none
    color: unset
    margin: 0
    list-style: none
body
    width: 100%
    font-family: 'Kumbh Sans', sans-serif
    scroll-behavior: smooth
    font-size: 16px
    font-weight: 400
    color: $color-text
    letter-spacing: -0.32px
    line-height: 24px

.container
    max-width: 1300px
    padding: 0 15px
    margin: 0 auto
    position: relative