@import "_variables"
@import "_settings"
@import "../../node_modules/swiper/swiper-bundle"

h1
    font-size: 72px
    font-weight: 800
    line-height: 120%
    color: $color-title
    max-width: 520px
    margin-bottom: 18px

h2
    font-size: 48px
    font-weight: 800
    color: $color-title
    line-height: 56px
    letter-spacing: -0.48px
h3
    font-size: 24px
    font-weight: 700
    line-height: 32px
    letter-spacing: -0.72px
    color: $color-title-h2
h4
    font-size: 19px
    font-weight: 700
    line-height: 24px
    color: $color-title
    margin-bottom: 8px
.logo
    width: 149px
    height: 27.31px

.btn
    font-size: 14px
    padding: 11px 16px
    background-color: $color-green
    outline: none
    border: none
    cursor: pointer
    border-radius: 4px
    transition: background-color 0.3s ease-in-out
    font-size: 14px
    text-align: center
    font-weight: 400
    color: #fff
    font-family: 'Kumbh Sans', sans-serif
    &:hover
        background-color: #157c6b
        
.subtitle
    color: $color-text
    font-size: 16px
    opacity: 0.4
    margin-bottom: 8px

.input
    padding: 10px 14px
    border-radius: 8px
    border: 1px solid #D0D5DD
    box-shadow: 0px 1px 2px 0px rgba(16, 24, 40, 0.05)
    margin-right: 16px
    outline: none
    &::-webkit-input-placeholder
        color: rgba(74, 85, 108, 0.50)
        font-family: 'Kumbh Sans', sans-serif

.under
    text-decoration: underline

.green-text
    color: $color-green
@import "components/_header"
@import "components/_top"
@import "components/_about"
@import "components/_comment"
@import "components/_blog"
@import "components/_free-guide"
@import "components/_place"
@import "components/_free-session"
@import "components/_subscribe"
@import "components/_footer"
@import "components/_top-services"
@import "components/_area"
@import "components/_features"
@import "components/_faq"
@import "components/_question"
@import "components/_top-free"
@import "components/_result"
@import "components/_transform"
@import "components/_download"
@import "components/_about-top"
@import "components/_company"
@import "components/_extra-top"
@import "components/_perfect"

@media only screen and (max-width: 1024px)
    h2
        font-size: 40px

@media only screen and (max-width: 800px)
    h2
        font-size: 30px
        line-height: 1.3
    h3
        font-size: 20px