.transform
    max-height: 520px
    background-color: $color-bg
    padding: 96px 0 160px
    margin-bottom: 350px
    &__wrapper
        display: flex
        align-items: flex-start
        justify-content: space-between
        margin-bottom: 100px
    &__left-wrapper
        max-width: 560px
    &__video
        position: relative
        display: flex
        align-items: center
        justify-content: center
        flex-direction: column
        margin: 0 auto
        max-width: 700px
        max-height: 400px
    &__icon
        background: none

.play__btn
    position: absolute
    cursor: pointer
    top: 130px
    z-index: 1
    height: 80px
    &.hidden
        display: none

@media screen and ( max-width:  996px)
    .transform
        padding: 56px 0 130px
        max-height: 650px
        &__wrapper
            flex-direction: column
        &__left-wrapper
            max-width: 100%
            margin-bottom: 30px
        &__video
            & video
                width: 700px
                height: 550px
    .play__btn
        top: 140px

@media screen and ( max-width:  768px)
    .transform
        max-height: 770px
        margin-bottom: 60px
        &__video
            max-width: 100%
            & video
                width: 500px
                height: 350px
        &__wrapper
            margin-bottom: 20px
    .play__btn
        width: 60px
        height: 60px
        top: 120px
    

@media screen and ( max-width:  556px)
    .transform
        margin-bottom: 50px
        max-height: 720px
        &__video
            & video
                width: 300px
                height: 200px
    .play__btn
        top: 65px  
        width: 50px
        height: 50px

@media screen and ( max-width: 440px)
    .transform
        margin-bottom: 40px
        max-height: 750px
        &__icon
            width: 40px
            height: 40px