.result
    margin-bottom: 120px
    &__title-wrapper
        display: flex
        justify-content: space-between
        align-items: center
        margin-bottom: 90px
    &__title
        margin-right: 30px
    &__btn
        width: 225px
    &__inner
        display: grid
        justify-content: center
        gap: 64px 32px
        grid-template-columns: 450px 450px
    &__item
        text-align: center
        padding: 20px
        border: 1px solid #EDEFF5
    &__stars
        margin: 0 auto
    &__item-text
        margin-bottom: 32px
    &__name
        font-size: 19px
        font-weight: 700
        color: $color-title
    &__company
        margin-bottom: 16px
        
@media screen and ( max-width:  996px)
    .result
        margin-bottom: 80px
        &__inner
            grid-template-columns: 350px 350px

@media screen and ( max-width:  768px)
    .result
        margin-bottom: 40px
        &__title-wrapper
            margin-bottom: 50px
        &__inner
            grid-template-columns: 1fr
            gap: 32px