.area
    text-align: center
    margin-bottom: 120px
    &__title
        margin-bottom: 80px
    &__item
        max-width: 450px
        padding: 56px 75px
        border: 1px solid #EDEFF5
    &__h3
        margin-bottom: 16px
    &__box
        display: grid
        justify-content: center
        gap: 20px
        grid-template-columns: 450px 450px
    &__img
        width: 78px
        height: 78px
        margin-bottom: 20px

@media screen and ( max-width:  996px)
    .area
        &__box
            grid-template-columns: 350px 350px
        &__item
            padding: 30px 45px
    
@media screen and ( max-width:  768px)
    .area
        margin-bottom: 60px
        &__title
            margin-bottom: 40px
        &__box
            grid-template-columns: 1fr
            justify-items: center
        &__img
            margin-bottom: 5px