.faq
    text-align: center
    margin-bottom: 64px
    &__h2
        margin-bottom: 20px
    &__descr
        margin-bottom: 64px
        font-size: 20px

.accordion
    text-align: left
    display: flex
    flex-direction: column
    &__title
        font-size: 18px
        color: $color-title-h2
        font-weight: 500
        cursor: pointer
        margin-right: 20px
    &__title-box
        cursor: pointer
        display: flex
        justify-content: space-between
        align-items: flex-start
    &__circle
        height: 25px
        width: 25px
        border-radius: 50%
        border: 3px solid $color-green
        padding-top: 7.5px
        padding-left: 4px
        flex-shrink: 0
        transition: transform 0.3s ease-in-out
        & span
            display: block
            transform-origin: 0% 0%
            width: 11px
            height: 3px
            background-color: $color-green
            transition: 0.3s ease-in-out
            &:last-child
                transform: rotate(90deg) translateX(-7px) translateY(-7px)
    &__circle.active
        transform: rotate(90deg)
        padding-left: 5px
        & span
            &:last-child
                display: none
                transition-delay: 0.3s
            &:first-child
                transform: rotate(90deg) translateX(-4px) translateY(-6px)
    &__text
        display: flex
        flex-direction: column
        gap: 8px
    &__descr
        padding: 0
        background-color: white
        max-height: 0
        overflow: hidden
        transition: max-height 0.3s ease-in-out
        &.active
            max-height: 50px
            display: block
    &__item
        margin-bottom: 24px
        padding-bottom: 32px
        border-bottom: 1px solid #E4E7EC
@media screen and ( max-width:  1300px)
    .accordion__circle
        padding-left: 3.5px
        padding-top: 7.5px
        &.active 
            padding-left: 4px
            & span
                &:first-child
                    transform: rotate(90deg) translateX(-4px) translateY(-7px)
    
@media screen and ( max-width:  900px)
    .accordion__circle
        padding-top: 8px
    .accordion__circle.active
        padding-left: 4px
        padding-top: 8px

@media screen and ( max-width:  768px)
    .faq
        &__descr
            font-size: 18px

@media screen and ( max-width:  700px)
    .accordion__circle
        padding-top: 7.5px

@media screen and ( max-width:  548px)
    .accordion__circle
        padding-top: 8px