.perfect
    background: $color-bg
    text-align: center
    padding-top: 80px
    padding-bottom: 80px
    margin-bottom: 80px
    &__h2
        max-width: 500px
        margin: 0 auto
        margin-bottom: 50px
    &__wrapper
        display: flex
        align-items: center
        justify-content: space-evenly
    &__icon
        width: 20px
        height: 20px
        margin-right: 5px
    &__item
        display: flex
        align-items: flex-start
        justify-content: space-between
        margin-bottom: 30px
    h3
        color: $color-title
        margin-bottom: 50px
    &__text
        font-size: 16px
        text-align: left
    &__left-wrapper
        max-width: 330px
        margin-right: 30px
    &__right-wrapper
        max-width: 330px

@media (max-width: 448px)
    .perfect
        &__h3
            font-size: 16px